<template>
  <div class="black-bg" v-if="모달창열렸니 == true">
    <div class="white-bg">
      <img :src="원룸들[누른거].image" class="room-img">
      <h4>{{원룸들[누른거].title}}</h4>
      <p>{{원룸들[누른거].content}}</p>
      <input v-model.number="month">
      <p>{{ month }} 개월 선택함 : {{원룸들[누른거].price * month}} 원</p>      
      <Discount/>
      <button @click="close">닫기</button>
    </div>  
  </div>
</template>

<script>
export default {
  name : 'Modal',
  data (){
    return{
      month : 1,
    }
  },
  watch : {
    month(a){
      if(a >= 13 ){
        alert('13이상 입력하지마라')
      }      
    }
  },
  props : {
    원룸들 : Array,
    누른거 : Number,
    모달창열렸니 : Boolean,
  },
  methods : {
    close(){
      this.$emit('closeModal', this.false)
    }
  },
  beforeUpdate(){
    if (this.month == 2){
      alert('최소 3개월 부터 가능')
    }
  }

}
</script>

